<template>
  <div>
    <div class="echartsDivTitle">仪表盘热度排行</div>
    <div class="imitationEchartsTooltip"
      :style="{top:tooltipTop,left:tooltipLeft}"
      v-show="tooltipBlCtrl">
      {{imitationObj.tooltipText}}<br />{{tooltipSon}}
    </div>
    <div class="imitationEcharts">
      <EasyScroll :barOption="srcoll">
        <div class="echartsSon"
          v-for="(item,num) in imitationData"
          :key="num">
          <span class="echartsName">
            <span :class="{echartsRankingNum1:num==1,echartsRankingName:true}">{{item.name}}</span>
          </span>
          <span class="echartsItem">
            <span :class="{'echartsDataItem':true,minNumberStyle:item.number==0,echartsDataItem1:num==0}"
              :style="{'width':item.width}"
              @mousemove="VIPRankingMousemove(num+1, $event)"
              @mouseleave="tooltipBl=false"
              @mouseenter="tooltipBl=true"></span>
          </span>
          <span :class="{echartsRight:true,echartsRight1:num==0&&item.number!=0}">{{item.number}}
          </span>
        </div>
      </EasyScroll>
    </div>
  </div>
</template>
<script>
export default {
	props: ['imitationData'],
	data() {
		return {
			// 分享列表下拉插件样式
			srcoll: {
				barColor: 'rgba(149, 160, 182)', //滚动条颜色
				barWidth: 6, //滚动条宽度
				railColor: 'rgba(71, 85, 102)', //导轨颜色
				barMarginRight: 0, //垂直滚动条距离整个容器右侧距离单位（px）
				barOpacityMin: 0.3, //滚动条非激活状态下的透明度
				horizrailenabled: false, //是否显示水平滚动条
				vBarHeight: '20vh'
			},
			imitationObj: { tooltipText: '仪表盘热度排行' },
			tooltipSon: '',
			itemOffsetX: 0,
			winX: 0,
			winY: 0,
			winWidth: window.innerWidth,
			imitationVIPRankingMaxVal: 100,
			tooltipBl: false,
			tooltipIndex: 1,
			winHeight: window.innerHeight
		};
	},
	methods: {
		// 仪表盘热度的鼠标悬停事件
		VIPRankingMousemove(index, event) {
			this.tooltipSon = `${this.imitationData[index - 1].name}：${this.imitationData[index - 1].number}`;
			this.winX = event.clientX;
			this.winY = event.clientY;
			this.tooltipIndex = index;
			this.itemOffsetX = event.offsetX;
		}
	},
	computed: {
		tooltipTop: function() {
			if (this.winY > this.winHeight * 0.37) {
				return this.winY - this.winHeight * 0.07 + 'px';
			} else {
				return this.winY + this.winHeight * 0.03 + 'px';
			}
		},
		tooltipLeft: function() {
			if (this.itemOffsetX > this.winWidth * 0.084) {
				return this.winX - this.winWidth * 0.04 + 'px';
			} else {
				return this.winX + 'px';
			}
		},
		tooltipBlCtrl: function() {
			if (parseInt(this.tooltipTop) > this.winHeight * 0.18 && this.tooltipBl) {
				return true;
			} else {
				return false;
			}
		}
	}
};
</script>
<style scoped>
