}
/* 模拟echarts样式 */
.imitationEcharts {
  width: 16.7vw;
  height: 22.1vh;
  position: absolute;
  top: 4vh;
  left: 0;
}
/* 每一个数据的样式 */
/* 左侧文字 */
.echartsSon {
  text-align: center;
  font-size: 0.6vw;
  width: 16.7vw;
  height: 4.5vh;
}
