<template lang="">
        <div class="shareIt">
          <div class="shareItTitle">
            <span>分享列表</span>
          </div>
          <div class="shareItTable">
            <div class="shareItTableTh">
              <table>
                <tr>
                  <th>报表</th>
                  <th>打开次数</th>
                  <th>状态</th>
                  <th>创建时间</th>
                  <th>失效时间</th>
                </tr>
              </table>
            </div>
            <div class="shareItTableTd">
              <!-- 一个封装好的滚动插件 -->
              <EasyScroll :barOption="srcoll"
        @xScrollToEnd
        = 'xScrollToEndFn' >
                <table>
                  <tr>
                    <th style="height:0;"></th>
                    <th style="height:0;"></th>
                    <th style="height:0;"></th>
                    <th style="height:0;"></th>
                    <th style="height:0;"></th>
                  </tr>
                  <!-- tableArr为临时数据，等待数据到来 -->
                  <tr v-for="(item,i) in tableArr"
                    :key="i">
                    <td style="color:#28b3fc;">&nbsp;&nbsp;&nbsp;&nbsp;{{item.repor}}</td>
                    <td style="color:#fff">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.openTimes}}</td>
                    <!-- 颜色，根据返回数据的状态判断 -->
                    <td :style="item.blean?'color:#00f8ff;':'color:rgb(227,105,132);'">
                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.status}}</td>
                    <!-- 固定颜色 -->
                    <td style="color:#00f8ff;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.createTime}}</td>
                    <!-- 固定颜色 -->
                    <td style="color:rgb(227,105,132);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.invalidTime}}</td>
                  </tr>
                </table>
              </EasyScroll>
            </div>
          </div>
        </div>
</template>
<script>
export default {
	data() {
		return {
			// 分享列表下拉插件样式
			srcoll: {
				barColor: 'rgba(149, 160, 182)', //滚动条颜色
				barWidth: 6, //滚动条宽度
				railColor: 'rgba(71, 85, 102)', //导轨颜色
				barMarginRight: 0, //垂直滚动条距离整个容器右侧距离单位（px）
				barOpacityMin: 0.3, //滚动条非激活状态下的透明度

				horizrailenabled: false, //是否显示水平滚动条
				vBarHeight: '20vh'
			},
			// 假数据
			tableArr: [],
			shareListPage: 1,
			shareListCount: 2,
			shareListBl: true
		};
	},
	methods: {
	  xScrollToEndFn() {
			if (this.shareListBl) {
				this.shareListBl = false;
				if (this.shareListPage + 1 <= this.shareListCount) {
					this.shareListPage += 1;
					this.getShareListFn();
				}
			}
		},
		// 初始化分享列表table的数据
		getShareListFn() {
			this.$get(`api/shareList?page=${this.shareListPage}&pagesize=10`).then(
				res => {
					if (res.code == '000000') {
						this.shareListBl = true;
						if (res.data) {
							if (res.data.count) {
								this.shareListCount = res.data.count;
							}
							for (let i = 0; i < res.data.share_data.length; i++) {
								this.tableArr.push({
									repor: res.data.share_data[i].name,
									openTimes: res.data.share_data[i].number,
									status: res.data.share_data[i].state,
									blean: true,
									createTime: res.data.share_data[i].create_time,
									invalidTime: res.data.share_data[i].valid_time
								});
							}
						}
					}
				}
			);
		}
	},
	mounted() {
		this.getShareListFn();
	}
};
</script>
<style scoped>
.shareItTitle {
  width: 100%;
  height: 3vh;
}

.shareItTitle > span {
  font-family: NotoSansHans-Regular;
  font-size: 0.8vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.9vw;
  letter-spacing: 0vw;
  color: #868fa3;
}

.shareItTable {
  height: 19vh;
}

.shareItTable .shareItTableTh {
  height: 3.8vh;
}

.shareItTable .shareItTableTd {
  height: 15.2vh;
}

.shareItTable .shareItTableTh table {
  width: 100%;
  height: 3.8vh;
  margin: 0;
  padding: 0;
  border: 0;
  border-collapse: collapse;
  text-align: left;
}

.shareItTable table tr th {
  font-size: 0.8vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.9vw;
  color: #a7b3cc;
  width: 13.24vw;
  height: 3.8vh;
}
.shareItTable .shareItTableTh table tr th {
  font-size: 0.8vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.9vw;
  letter-spacing: 0vw;
  color: #a7b3cc;
  width: 13.24vw;
  height: 3.8vh;
  background-color: #232e40;
  padding-left: 1.5em;
}

.shareItTable table tr td {
  font-family: NotoSansHans-Regular;
  font-size: 0.8vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 3.8vh;
  color: #00f8ff;
}

.visualization {
  width: 19vw;
  height: 91vh;
  float: right;
}
</style>
