	mounted() {
		this.$get('api/centerControl').then(res => {
			if (res.code == '000000') {
				const findData = function(str) {
					if (str) {
						return str;
					} else {
						return 0;
					}
				};
				this.today_visitor.amount = findData(res.data[0].today_visitor);
				this.total_visitor.amount = findData(res.data[0].total_visitor);
				this.projectSum.amount = findData(res.data[0].projectSum);
				this.shareSum.amount = findData(res.data[0].shareSum);
				let heatData = res.data[0].heatProject;
				// 设备来源分析数据
				this.deviceProportion.series[0].data = [
					{ name: 'PC端', value: findData(res.data[0].pc_device) },
					{ name: '平板', value: findData(res.data[0].ipad_device) },
					{ name: '移动端', value: findData(res.data[0].mobile_device) }
				];
				// 冒泡排序
				function BubbleSort(arr) {
					let n = arr.length;
					for (let i = 0; i < n - 1; i++) {
						for (let j = 0; j < n - 1 - i; j++) {
							if (!arr[j].number) {
								arr[j].number = 0;
							}
							if (!arr[j + 1].number) {
								arr[j + 1].number = 0;
							}
							if (arr[j].number < arr[j + 1].number) {
								let temp = arr[j];
								arr[j] = arr[j + 1];
								arr[j + 1] = temp;
							}
						}
					}
					return arr;
				}
				let getMimeDataFn = function(arr, maxNum, type) {
					let newArr = [];
					if (arr) {
						if (type == 0) {
							for (let i = 0; i < arr.length; i++) {
								arr[i].width = '100%';
								arr[i].number = 0;
							}
							newArr = arr;
						} else {
							for (let i = 0; i < arr.length; i++) {
								if (arr[i].number == 0) {
									break;
								}
								arr[i].width = parseInt((arr[i].number * 100) / maxNum) + '%';
								newArr.push(arr[i]);
							}
						}
					}
					return newArr;
				};
				  console.log(heatData);
				if (heatData.length > 0) {
					heatData = BubbleSort(heatData);
					if (heatData[0].number == 0 || !heatData[0].number) {
						this.imitationData = getMimeDataFn(heatData, 100, 0);
					} else {
						this.imitationData = getMimeDataFn(heatData, heatData[0].number, 1);
					}
				} else {
					this.imitationData = [];
				}
				// 地图
				let initMapData = res.data.visitorStatus||[];
				let MapData = [];
				for (let i = 0; i < initMapData.length; i++) {
					initMapData[i].site = gcj02tobd09(
						initMapData[i].site[0],
						initMapData[i].site[1]
					);
					initMapData[i].site.push(initMapData[i].Number);
					MapData.push({
						name: initMapData[i].siteName,
						value: initMapData[i].site
					});
				}
				this.happening.series[1].data = MapData;

				// 项目结构
				let distribution = res.data[0].structure;
				let distributionData = [];
				for (let i = 0; i < distribution.length; i++) {
					if (!distribution[i].varlue && distribution[i].name) {
						distribution[i].varlue = 0;
					}
					distributionData.push({
						name: distribution[i].name,
						value: distribution[i].varlue
					});
				}
				this.distribution.series[0].data = distributionData;
			}
		});
		if (!localStorage.getItem('indexDialog')) {
			localStorage.setItem('indexDialog', false);
			return;
		} else if (localStorage.getItem('indexDialog') === 'false') {
			this.indexDialog = false;
		}
	}
};
