<template>
  <div>
    <div class="searchInput"
      v-click-outside="{fn:searchInputBlur}">
      <!-- 搜索框：
			绑定class：判定是否选中，选中变色
			v-model：绑定搜索框的内容
			keyup：所有键盘事件，用于与下方下拉框联动
			keyup.enter：回车事件，直接搜索
			focus：选中变色并判断是否有历史搜索记录，变色，如果有历史搜索记录，下拉框显示
			blur：关闭下拉框，恢复颜色 -->
      <input type="text"
        placeholder="搜索可视化"
        :class="{'inputBl':inputBl}"
        v-model="searchInputVal"
        @keyup="searchInputKeyup"
        @keyup.enter="searchClick"
        @focus="searchInputFocus">
      <!-- 搜索按钮，鼠标移入移出切换，点击后如果内容不为空，开始搜索 -->
      <span class="searchBtn biFont icon-search vCenter"
        @click="searchClick"></span>

      <!-- 下拉框，如果没有历史搜索记录，则不显示 -->
      <div class="HomepageList"
        v-show="HomepageListBl">
        <EasyScroll :barOption="srcoll">	
          <ul>
            <!-- HomepageListArr会随着用户的输入改变   -->
            <!-- 点击传入值，并开始搜索 -->
            <li v-for="(val,i) in HomepageListArr"
              :key="i"
              @click="HomepageListClick(val)">{{val}}</li>
          </ul>
        </EasyScroll>
      </div>
    </div>
    <!-- 展示可视化，solutionsArr会随着搜索请求结果而改变 -->
    <div class="solutionsDiv"
      v-for="(item,index) in solutionsArr"
      :key="index">
      <!-- div中的按钮图片 -->
      <div class="solutionsDivImg"
        @mouseenter="solutionsBlObj[index].solutionsDivImgBlean=true">
        <img :src="item.screenShot"  alt>
        <div class="hoverDiv"
          @mouseleave="solutionsBlObj[index].solutionsDivImgBlean=false"
          v-show="solutionsBlObj[index].solutionsDivImgBlean">
          <img v-show="!solutionsBlObj[index].hoverDiv1Img1Blean"
            @mouseleave="solutionsBlObj[index].hoverDiv1Img1Blean=true"
            class="hoverDivImg2"
            src="@/assets/Main/img/-preview3-blue.png"
            @click="previewSreen(item)"
            alt>
          <img v-show="solutionsBlObj[index].hoverDiv1Img1Blean"
            @mouseenter="solutionsBlObj[index].hoverDiv1Img1Blean=false"
            class="hoverDivImg2"
            src="@/assets/Main/img/-preview3.png"
            alt>
          <!-- 点击事件，点击携带id和name跳转 -->
          <img v-show="!solutionsBlObj[index].hoverDiv1Img2Blean"
            @mouseleave="solutionsBlObj[index].hoverDiv1Img2Blean=true"
            class="hoverDivImg1"
            src="@/assets/Main/img/-edit2-blue.png"
            @click="toScreen(item.id,item.name)">
          <img v-show="solutionsBlObj[index].hoverDiv1Img2Blean"
            @mouseenter="solutionsBlObj[index].hoverDiv1Img2Blean=false"
            class="hoverDivImg1"
            src="@/assets/Main/img/-edit2.png">
        </div>
      </div>
      <!-- 文字内容 -->
      <div class="solutionsDivTxt">
        <h5>{{item.name}}</h5>
        <span>{{item.time}}</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
	data() {
		return {
			// 搜索框图片切换
			searchImgBl: true,
			// 下拉框bl
			HomepageListBl: false,
			// 搜索框样式bl
			inputBl: false,
			// 搜索框内容
			searchInputVal: '',
			// 展示可视化的数组，请求返回数据后赋值
			solutionsArr: [],
			cacheSolutionsArr: [],
			// 图片按钮bl
			solutionsBlObj: {
				0: {
					solutionsDivImgBlean: false,
					hoverDiv1Img1Blean: true,
					hoverDiv1Img2Blean: true
				},
				1: {
					solutionsDivImgBlean: false,
					hoverDiv1Img1Blean: true,
					hoverDiv1Img2Blean: true
				},
				2: {
					solutionsDivImgBlean: false,
					hoverDiv1Img1Blean: true,
					hoverDiv1Img2Blean: true
				}
			},
			// 展示搜索记录的数组，请求返回数据后赋值
			HomepageListArr: []
		};
	},
	computed: {
		vwToPx() {
			return this.$store.getters['main/vwToPx'];
		},
		srcoll() {
			return {
				barColor: 'rgba(149, 160, 182)', //滚动条颜色
				barWidth: 6, //滚动条宽度
				railColor: 'rgba(71, 85, 102)', //导轨颜色
				barMarginRight: 0, //垂直滚动条距离整个容器右侧距离单位（px）
				barOpacityMin: 0.3, //滚动条非激活状态下的透明度
				zIndex: 'auto',
				vBarHeight: '20vh',
				borderWidth: this.vwToPx(0.12)
			};
		}
	},
	methods: {
		// 初始化右侧可视化区域数据
		init() {
			this.$get('api/searchProject?num=3').then(res => {
				if (res.code == '000000') {
					this.solutionsArr = res.data;
					console.log(this.solutionsArr);
					this.cacheSolutionsArr = res.data;
				}
			});
		},
		// enter键键盘事件和搜索按钮点击事件
		searchClick() {
			let vm = this;
			// 用户输入信息有效，进行下一步
			let searchFn = () => {
				this.$get(`api/searchProject?search=${this.searchInputVal}`).then(
					res => {
						if (res.code == '000000') {
							this.solutionsArr = res.data;
						}
					}
				);
				let bl = false;
				let arr = [];
				if (localStorage.getItem('searchArr')) {
					arr = localStorage.getItem('searchArr').split(',');
				}

				for (let i = 0; i < arr.length; i++) {
					if (vm.searchInputVal == arr[i]) {
						bl = true;
					}
				}
				if (!bl) {
					arr.push(vm.searchInputVal);
				}
				localStorage.searchArr = arr;
			};
			if (vm.searchInputVal == '') {
				return;
			} else {
				searchFn();
			}
		},
		// 搜索框聚焦事件
		searchInputFocus() {
			// 如果有搜索记录，显示搜索历史
			let vm = this;
			let val = this.searchInputVal;
			if (localStorage.getItem('searchArr')) {
				// 聚焦后，如果有历史搜索记录，遍历出来并显示下拉框
				this.HomepageListArr = localStorage.getItem('searchArr').split(',');
				this.HomepageListBl = true;
				this.inputBl = true;
				let arr = localStorage.getItem('searchArr').split(',');
				let arr2 = [];
				if (val != '') {
					for (let i = 0; i < arr.length; i++) {
						if (arr[i].indexOf(val) != -1) {
							arr2.push(arr[i]);
						}
					}
					vm.HomepageListArr = arr2;
					if (vm.HomepageListArr.length < 1) {
						vm.HomepageListBl = false;
					} else {
						vm.HomepageListBl = true;
					}
				} else {
					vm.HomepageListArr = localStorage.getItem('searchArr').split(',');
				}
			} else {
				this.inputBl = true;
			}
		},
		// 搜索框失焦事件
		searchInputBlur() {
			this.inputBl = false;
			this.HomepageListBl = false;
			this.searchClick();
		},
		// 搜索框键盘抬起事件，匹配用户搜索过的内容
		searchInputKeyup() {
			let vm = this;
			let val = this.searchInputVal;
			// 判断localStorage是否存在搜索记录
			if (localStorage.getItem('searchArr')) {
				// 把搜索记录取出来放到数组里
				let arr = localStorage.getItem('searchArr').split(',');
				let arr2 = [];
				// 判断用户输入的内容是否为空
				if (val != '') {
					// 与用户输入的内容相符的记录显示出来
					for (let i = 0; i < arr.length; i++) {
						if (arr[i].indexOf(val) != -1) {
							arr2.push(arr[i]);
						}
					}
					vm.HomepageListArr = arr2;
					// 判断用户输入的内容与缓存里的内容相符合的值是否为空,为空则不显示下拉框
					if (vm.HomepageListArr.length < 1) {
						vm.HomepageListBl = false;
					} else {
						vm.HomepageListBl = true;
					}
				} else {
					// 用户没有输入内容,全部记录展示
					vm.HomepageListArr = localStorage.getItem('searchArr').split(',');
				}
			}
		},
		// 搜索按钮的点击事件,获取值并发起请求
		HomepageListClick(inputVal) {
			if (inputVal) {
				this.searchInputVal = inputVal;
				this.inputBl = false;
				this.HomepageListBl = false;
				this.$get('api/searchProject?search=' + inputVal).then(res => {
					if (res.code == '000000') {
						// 改变可视化区域展示的内容
						this.solutionsArr = res.data;
					}
				});
			}
		},
		// 右侧搜索结果的点击事件,点击并且把id,name放在localStorage里,携带id跳转
		toScreen(itemId, itemName) {
			let vm = this;
			localStorage.setItem('fatherProjectId', itemId);
			localStorage.setItem('fatherProjectName', itemName);
			if (localStorage.getItem('fatherProjectId')) {
				vm.$router.push({
					name: 'screen',
					params: {
						id: localStorage.getItem('fatherProjectId')
					}
				});
			}
		},
		previewSreen(item){
		  let vm=this;
		  let curFatherId=item.id;
			async function doIt(curFatherId) {
				const projectSn = await getFirstProjectSn(curFatherId);
				const previewCurProject = await getPreviewCurProject(projectSn);
				if (previewCurProject) {
					const address = `${window.location.host}/previewPages?share_sn=${previewCurProject.share_sn}&password=${previewCurProject.password}`;
					window.open(window.location.protocol + '//' + address, '_blank');
				}
			}

			function getFirstProjectSn(curFatherId) {
				return new Promise((resolve, reject) => {
					vm.$get('api/project', {projectId: curFatherId})
						.then(res => {
							if (res.code == '000000') {
								resolve(res.data[0]['id']);
							} else {
								vm.tips.show([res.err_msg]);
								reject(res.err_msg);
							}
						})
						.catch(error => {
							console.error(error);
						});
				});
			}

			function getPreviewCurProject(curProjectSn) {
				return new Promise((resolve, reject) => {
					const shareObj = {
						project_sn: curProjectSn,
						share_type: 1,
						is_pwd: 0
					};
					vm.$get('api/shareUnique', shareObj)
						.then(res => {
							if (res.code == '000000') {
								resolve(res.data);
							} else {
								reject(res.err_msg);
							}
						})
						.catch(error => {
							vm.tips.show(['获取预览链接失败']);
							console.error(error);
						});
				});
			}

			doIt(curFatherId);
		}
	},
	mounted() {
		this.init();
	},
	watch: {
		searchInputVal(newVal) {
			if (newVal == '') {
				this.solutionsArr = this.cacheSolutionsArr;
			}
		}
	}
};
</script>
<style scoped>
.searchInput {
  width: 19vw;
  height: 3.96vh;
  background-color: #798194;
  position: relative;
}
.searchInput .inputBl {
  border: 0.0625vw solid #3dadff;
}
.searchBtn {
  font-size: 0.8vw;
  color: #99a4bb;
  cursor: pointer;
  right: 0.5vw;
}
.searchBtn:hover {
  color: rgb(40, 179, 252);
}

/* .HomepageSearch img {
  position: absolute;
  cursor: pointer;
  top: 1vh;
  width: 2vh;
  height: 2vh;
} */

.solutionsDiv {
  background-color: #1c2433;
  width: 19vw;
  height: 27vh;
  padding: 0.8vw 0.8vw 0.8vw 0.8vw;
  margin-top: 2vh;
  cursor: pointer;
}

.solutionsDivImg {
  width: 17.4vw;
  height: 18.5vh;
  background-color: #fff;
  margin-bottom: 1vh;
  position: relative;
}

.solutionsDivImg img {
  width: 17.4vw;
  height: 18.5vh;
}

.hoverDiv {
  position: absolute;
  width: 17.4vw;
  height: 18.5vh;
  top: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.2);
}

.hoverDiv .hoverDivImg1 {
  position: absolute;
  left: 4.8vw;
  cursor: pointer;
  top: 9vh;
  width: 4vh;
  height: 4vh;
}

.hoverDiv .hoverDivImg2 {
  position: absolute;
  right: 4.8vw;
  cursor: pointer;
  top: 9vh;
  width: 4vh;
  height: 4vh;
}

.solutionsDivTxt h5:hover {
  color: #3dadff;
}
.solutionsDivTxt {
  margin-top: 1.6vh;
}
.solutionsDivTxt h5 {
  font-family: NotoSansHans-Regular;
  font-size: 0.9vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1vw;
  letter-spacing: 0vw;
  color: #ffffff;
  margin-bottom: 0.7vh;
  cursor: pointer;
}

.solutionsDivTxt span {
  font-family: NotoSansHans-Regular;
  font-size: 0.6vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1vw;
  letter-spacing: 0vw;
  color: #8e98ad;
}

.echartsDivTitle {
  font-size: 0.8vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.9vw;
  letter-spacing: 0vw;
  color: #868fa3;
  position: absolute;
  top: 1vh;
  left: 1vh;
  z-index: 9;
}

.h5 {
  cursor: pointer;
}

.searchInput .inputBl {
  border: 0.0625vw solid #3dadff;
}
.HomepageList {
  width: 19vw;
  max-height: 15vh;
  height: 15vh;
  position: absolute;
  bottom: -15vh;
  z-index: 3;
  background-color: #1c2433;
  border: 0.0625vw solid rgb(74, 89, 105);
  left: 0;
  cursor: pointer;
}

.HomepageList li {
  padding-left: 0.5vw;
