<template>
  <div>
    <div class="echartsDivTitle">访客分布</div>
    <div class="mapMoving"
      :style="[obj.mapMovingStyle]"
      v-if="obj.mapMovingStyleBl">
      <div class="mapMovingText">
        <span>
          <span>{{obj.mapMovingObj.project_name}}</span>
        </span>
        <div class="rimLeftTop"></div>
        <div class="rimLeftBottom"></div>
        <div class="rimRightTop"></div>
        <div class="rimRightBottom"></div>
        <span class="browseInformation">
          <span class="square"></span>
          <span>{{obj.mapMovingObj.browser}}</span>
        </span>
        <span class="browseInformation">
          <span class="square"></span>
          <span>{{obj.mapMovingObj.device}}</span>
        </span>
        <span class="browseInformation">
          <span class="square"></span>
          <span>{{obj.mapMovingObj.address}}</span>
        </span>
        <span class="browseInformation"
          v-if="obj.mapMovingObj.time">
          <span class="square"></span>
          <span>时间：{{obj.mapMovingObj.time}}</span>
        </span>
      </div>
      <div class="triangle"></div>
    </div>
    <!-- 设备使用占比echarts表格 -->
    <v-chart :options="options"
      ref="happening"
      style="width:34.2vw;height:54vh;"></v-chart>
  </div>
</template>
<script>
import { gcj02tobd09 } from '../../../utils/posConvert';
import vECharts from 'vue-echarts';
export default {
	components: { 'v-chart': vECharts },
	props: ['options'],
	data() {
		return {
			obj: {
				mapMovingStyle: {
					top: '12px',
					left: '0px'
				},
				mapMovingStyleBl: false,
				mapMovingObj: {
					project_name: '',
					address: '访客地点',
					browser: '访客浏览器',
					device: '访客设备',
					time: '访客时间'
				}
			}
		};
	},
	watch: {
		'obj.mapMovingObj': {
			handler(newVal) {
				if (newVal) {
					setTimeout(() => {
						this.obj.mapMovingStyleBl = false;
						this.options.series[0].data = [];
					}, 5000);
				}
			},
			immediate: true
		}
	},
	sockets: {
		share_Push(res) {
			// 假定经纬度
			var vm = this;
			// 转换数据
			var mapMovingObj = JSON.parse(res).data;
			// 如果数据没有经纬度，退出
			if (!mapMovingObj.latlag[0]) {
				return;
			}
			// 经纬度转为数组
			mapMovingObj.latlag = mapMovingObj.latlag.split(',');
			// 经纬度高德转百度
			mapMovingObj.latlag = gcj02tobd09(
				mapMovingObj.latlag[0],
				mapMovingObj.latlag[1]
			);
			// 转换echarts坐标到改变弹窗位置的函数
			var getSiteFn = function(longitude, latitude) {
				var arr = vm.$refs.happening.convertToPixel({ seriesName: '访客' }, [
					longitude,
					latitude
				]);
				vm.obj.mapMovingStyle.top = 'calc(' + arr[1] + 'px - 17.3vh)';
				vm.obj.mapMovingStyle.left = 'calc(' + arr[0] + 'px' + ' - 7.3vw)';
			};
			// 假定经纬度没有一样的点
			var bl = false;
			for (let i = 0; i < vm.options.series[1].data.length; i++) {
				// 判断经纬度是否有一致的，有的有则改变bl的值
				if (
					vm.options.series[1].data[i].value[0] == mapMovingObj.latlag[0] &&
          vm.options.series[1].data[i].value[1] == mapMovingObj.latlag[1]
				) {
					bl = true;
					// 先把echarts中的data获取下来，然后再加进去，否则无效---echarts不会重新渲染
					var arr = vm.options.series[1].data[i];
					arr.value[2] = arr.value[2] + 1;
					vm.options.series[1].data.splice(i, 1);
					vm.options.series[1].data.push(arr);
					// 是否弹窗显示，如果已经显示，则不显示
					if (!vm.obj.mapMovingStyleBl) {
						vm.obj.mapMovingObj = mapMovingObj;
						vm.options.series[0].data.push(arr);
						getSiteFn(mapMovingObj.latlag[0], mapMovingObj.latlag[1]);
						vm.obj.mapMovingStyleBl = true;
					}
					return;
				}
			}

			if (!bl) {
				mapMovingObj.latlag.push(1);
				vm.options.series[1].data.push({
					name: mapMovingObj.siteName,
					value: mapMovingObj.latlag
				});
				if (!vm.obj.mapMovingStyleBl) {
					vm.mapMovingObj = mapMovingObj;
					vm.options.series[0].data.push({
						name: mapMovingObj.siteName,
						value: mapMovingObj.latlag
					});
					getSiteFn(mapMovingObj.latlag[0], mapMovingObj.latlag[1]);
					vm.obj.mapMovingStyleBl = true;
				}
				return;
			}
		}
	}
};
</script>
<style scoped>
.echartsDivTitle {
  font-size: 0.8vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.9vw;
  letter-spacing: 0vw;
  color: #868fa3;
  position: absolute;
  top: 1vh;
  left: 1vh;
  z-index: 9;
}
.mapMoving {
  position: absolute;
  width: 14.6vw;
  height: 17.3vh;
  z-index: 9;
}
.square {
  width: 0.3vw;
  height: 0.3vw;
  background-color: #fc718e;
  display: inline-block;
  position: relative;
  bottom: 0.2vh;
  margin-left: 0.9vw;
  margin-right: 0.7vw;
}
.rimLeftTop {
  position: absolute;
  top: 0;
  left: 0;
  width: 0.6vw;
  height: 0.6vw;
  border-top: 0.1vw solid #fc718e;
  border-left: 0.1vw solid #fc718e;
}
.rimLeftBottom {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0.6vw;
  height: 0.6vw;
  border-left: 0.1vw solid #fc718e;
  border-bottom: 0.1vw solid #fc718e;
}
.rimRightTop {
  position: absolute;
  top: 0;
  right: 0;
  width: 0.6vw;
  height: 0.6vw;
  border-top: 0.1vw solid #fc718e;
  border-right: 0.1vw solid #fc718e;
}
.rimRightBottom {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 0.6vw;
  height: 0.6vw;
  border-bottom: 0.1vw solid #fc718e;
  border-right: 0.1vw solid #fc718e;
}
.mapMovingText {
  width: 14.6vw;
  height: 15.3vh;
  background-color: rgba(0, 0, 0, 0.3);
  position: relative;
}
.mapMovingText > span > .dele {
  float: right;
  padding-right: 0.8vw;
  font-size: 0.6vw;
  line-height: 0.6vw;
}
.icon-yuandian {
  color: #fc718e;
  font-size: 0.6vw;
  line-height: 0.7vw;
  margin: 0 0.9vw;
}
.browseInformation {
  color: #fff;
  font-size: 0.7vw;
  line-height: 0.7vw;
  display: block;
  margin-bottom: 1vh;
}
.mapMovingText > span:nth-child(1) {
  font-size: 0.8vw;
  color: #fc718e;
  line-height: 0.8vw;
  padding: 1.3vh 0 0 0.9vw;
  display: block;
  height: 4.8vh;
  width: 14.6vw;
}
.triangle {
  width: 0;
  height: 0;
  border-left: 0.5vw solid transparent;
  border-right: 0.5vw solid transparent;
  border-top: 0.5vw solid rgba(34, 45, 63, 0.8);
  margin-left: 6.8vw;
}
</style>
